





















































































import {Vue} from 'vue-property-decorator';
import {Loading} from 'quasar';
import cfg from "../../heating-config";
import axios from 'axios';


export default Vue.extend({
  data() {
    return {
      name: "",
      refCd: "",
      orderId: 1,
      deviceId: "",
      selected: [],
      freeDeviceIds: [],
      addPopupDisplayed: false,
      tempSeries: [],
      fromDate: new Date(),
      toDate: new Date(),
      tempColumns: [
        {
          name: 'name',
          label: 'Pomenovanie meracieho miesta',
          align: 'left',
          field: 'name',
          sortable: true
        },
        {
          name: 'refCd',
          label: 'Unikátny kľúč',
          align: 'left',
          field: 'refCd',
          sortable: true
        },{
          name: 'orderId',
          label: 'Poradie',
          align: 'left',
          field: 'orderId',
          sortable: true
        }, {
          name: 'deviceId',
          label: 'ID zariadenia',
          align: 'left',
          field: 'deviceId',
          sortable: true
        }
      ],
      data: []
    }
  },
  methods: {
    loadCurrentState() {
      Loading.show();
      axios.get(cfg.BASE_URL + "temp/measurePlace")
          .then(response => {
            this.data = response.data;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            // eslint-disable-next-line
            console.log(error)
          });
      axios.get(cfg.BASE_URL + "temp/freeDeviceIds")
          .then(response => {
            var tmp:Array<any>=new Array<any>();
            for (var i=0; i< response.data.length;i++){
              tmp.push({"value":response.data[i],"label":response.data[i]});
            }
            this.freeDeviceIds = tmp;
            Loading.hide();
          })
          .catch(error => {
            Loading.hide();
            // eslint-disable-next-line
            console.log(error)
          });
    },
    saveMeasureplace() {
      let data: any = {};
      data['name'] = this.name;
      data['deviceId'] = this.deviceId;
      data['orderId'] = this.orderId;
      axios.post(cfg.BASE_URL + "temp/measurePlace", data, {method: "post"})
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            // eslint-disable-next-line
            console.log(error)
          });
    },
    editMeasureplace(row: any) {
      axios.put(cfg.BASE_URL + "temp/measurePlace/" + row.refCd, row, {method: "PUT"})
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            // eslint-disable-next-line
            console.log(error)
          });
    },
    deleteMeasurePlace() {
      axios.delete(cfg.BASE_URL + "temp/measurePlace/" + this.selected[0].refCd)
          .then(response => {
            this.loadCurrentState();
          })
          .catch(error => {
            // eslint-disable-next-line
            console.log(error)
          });
    }
  },
  mounted(): void {
    let date = new Date();
    date.setDate(this.fromDate.getDate() - 7);
    this.fromDate = date;
    this.loadCurrentState();
  }
});
